

.App {
  display: flex;
  flex-direction: column;
  width: 100%; 
  height: 100%; 


}


.App-Inputs {
  border: 1px solid black;
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: 20vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0 3vh;



}

.App-Keyboard {
  border: 1px solid black;
  flex-basis: 23vh;


}

#keyboard-cont {

  display: flex;
  flex-direction: column;
  align-items: center;
}

#keyboard-cont div {
  display: flex;
}

.second-row {
  margin: 1vh 0;
}

.App-Keyboard button {
  font-size: 4vmin;

  padding: 1.7vh;

  cursor: pointer;
  text-transform: uppercase;
  background: none;
	color: inherit;
	border: none;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}

.App-Images {
  border: 1px solid black;
  flex-grow: 0;

  flex-shrink: 0;
  flex-basis: 57vh;
  display: flex;
  flex-direction: column;


}

.App-box:before{
  content: '';
  display: block;
  padding-top: 100%;

}
.App-photo1 {

  height: 16vh;
  width: 47vw;
  padding: 1vh;



}

.App-photo1 img {
  max-width: 100%;
  max-height: 100%;
  border-radius: 3px;
  display: block;
  margin-left: auto;
  margin-right: auto;

}
.App-photo1 p {
  text-align: center;
  color: grey;
  font-size: 10px;


}

